.home__icon h1{
    font-size:30px;
    color: var(--main-orange);
    letter-spacing: 1px;
    font-weight: lighter;
}

.home__icon h1:hover{
    text-decoration: underline;
    -webkit-text-decoration-color: var(--main-orange);
            text-decoration-color: var(--main-orange);
