@media (min-width: 50em) {
    .navbar ul {
        margin: 0 -10px;
        border-bottom: 1px solid var(--nav-border);
        align-items: center;
    }
    nav li {
        margin: 0 20px;
    }
    .home__icon {
        margin-right: auto;
    } 
}

@media (min-width:50em) {
    .list__items {
        display: flex;
        padding-inline: 2rem;
        padding-block: 1.5rem;
        grid-gap: 3rem;
        gap: 3rem;
        list-style: none;
        background: var(--blur);
        -webkit-backdrop-filter: blur(.3rem);
                backdrop-filter: blur(.3rem);
        margin: 0;
    }

    .menu_icon {
        display: none;
    }

    .close_icon{
        display: none;
    }


}

.list__items li a {
    text-decoration: none;
    color: var(--idea-grey);
    font-size: 17px;
    font-family: Righteous;
}

.nav__links{
    margin-left: 10rem;
    flex:auto;
}

.list__items li a:hover {
    text-decoration: underline;
}

/* @media (width: 50em){

} */

@media (max-width:50em) {
    .navbar {
        display: flex;
    }
    .list__items {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        position: fixed;
        inset: 0 0 0 0%;

        grid-gap: 1.5rem;

        gap: 1.5rem;
        list-style: none;        

        background-color: var(--img-back);
        border: 1px solid var(--nav-border);

        transform: translateY(100%);
        transition: 400ms;
    }

    .list__items.active {
        transform: translateX(0);
        transition: 450ms;
    }

    .menu_icon {
        position: absolute;
        right: 2rem;
        top: 1.5rem;
        color: var(--main-black);
        cursor: pointer;
    }

    .menu_icon .menu {
        font-size: 40px;
    }
    .close_icon {
        color: var(--main-black);
        cursor: pointer;
        position: fixed;
        top: 2rem;
        right: 2rem;
    }
}
.ideas {
  width: 100%;
  min-height: 100vh;
  background: var(--img-back);
  position: relative;
  overflow: hidden;
}

.bookshelf {
  margin-left: auto;
  margin-right: auto; 
  max-width: min(800px, 90%);
  margin-bottom: 50px;
}



.cards {
  padding-top: 1%;
  /* margin-left: 23rem; */
}


.cards h1 {
  color: var(--main-purple);
  font-family: Righteous;
  font-size: 50px;
  font-weight:lighter;
  /* text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 7px; */
  /* text-decoration-color: var(--idea-grey-underline); */
  margin-top:3rem;
}

.cards h3 pre, span, a {
  font-family: Monaco;
  font-weight: 100;
  font-size: 14px;
}

.LinkListItem{
  margin-bottom: 30px; 
  margin-left: 15px;
}

.standardized-spacing {
  grid-column: span 2;
  grid-row: span 2;
}

@media (min-width:900px) {
  .ideas__container {
      display: grid;
      margin-block: 1rem;
      grid-template-columns: repeat(auto-fit,minmax(35rem,1fr));
      grid-gap: 1rem;
      gap: 1rem;
  }
}

@media (max-width:900px) {
  .ideas__container {
      padding:20px;
      grid-gap:2rem;
      gap:2rem;
  }
  .cards {
      margin-block: 1rem;
  }
}
