* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  :root {
  --main-black:#000000;
  --main-orange:#FF5C00;
  --main-purple:#5643E9;
  --nav-border: #c6c6c6;
  --img-back:#FFFFF4;
  --glass-white:rgba(106, 101, 101, 0.214),rgba(112, 107, 112, 0.344); 
  --glass-secondary:rgb(87, 10, 94),rgb(143, 64, 50);
  --glass-before:rgba(16, 15, 15, 0.146);
  --blur:hsla(0, 0%, 100%, 0.03);
  --idea-grey: #5F5C76;
  --idea-grey-underline: #a2a2a2;
  }
  
  *::after,*::before {
  box-sizing: border-box;
  }
  
  img,picture {
   max-width: 100%;
  }


.home {
    background: var(--img-back);
    width: 100%;
    min-height: 100vh;
    background-size: cover;
    background-position: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    font-family: Righteous;
}

.home__box {
    display: flex;
    align-items: center;
    margin: 0rem 6rem;
    position: relative;
}

@media (min-width:1000px) {
    .home__img {
        width:720px;
        margin-left: 1rem;
    }
    .home__content {
        margin-left: 1rem;
        width: 550px;
    }
    .content__header {
        margin-left: 20%;
        font-size: 40px;
        font-weight: lighter;
        color: var(--main-orange);
        text-transform: lowercase;
        line-height: 75px;
        text-decoration: underline;
        text-decoration-thickness: 1px;
        text-underline-offset: 7px;
        -webkit-text-decoration-color: var(--main-orange);
                text-decoration-color: var(--main-orange);
        letter-spacing: 1px;
    }

    .home__content nav{
        color: var(--idea-grey);
    }
    
    .home__content nav ul{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-right:20%;
    }
    .home__content nav li{ 
        list-style: none;
        padding-inline: 1rem;
        line-height: 35px;
    }
    
    .home__content nav li a{
        text-decoration: none;
        color: var(--idea-grey);
        font-size: 17px;
        font-family: Righteous;
    }
    
    .home__content nav li a:hover{
